import React, { FC, useRef, useState, useEffect } from 'react';
import QueryTable, { Props as QueryTableProps } from '@/components/QueryTable';
import { ColumnsType } from 'antd/lib/table';
import { withAuthorized } from '@/utils/Authorized';
import { Role } from '@/utils/authority';
import { Link } from 'umi';
import {
  REPORT_DEAL_STATUS,
  REPORT_TYPE,
  REPORT_CHECK_STATUS,
} from '@/utils/constant.js';
import {
  getRecommendationList,
  dealCommentReport,
  queryCommentDetail,
} from './services';
import { batchGetDramaContentById } from '@/pages/ContentManage/HotManage/services';
import { Button, message, Space } from 'antd';
import { ListQueryRequestBody } from '@/types/types';
import { ReportUserInfo } from './types';
import { ResponseData } from '@/types/types';
import { APP_ID, isListContent } from '@/utils/constant.js';
import { useGetProductionGroupDetail } from '@/services/publicServices';

const statusFontColor = {
  '0': 'rgba(0, 0, 0, 0.85)',
  '1': '#ff4d4f',
  '2': 'gray',
};

const ReportList: FC = () => {
  const formRef = useRef<any>();
  const queryTable = useRef<any>();
  const [list, setList] = useState<any[]>([]);
  const [totalCount, setTotalCount] = useState(0);
  const [loading, setLoading] = useState(false);
  const { productionGroupDetail = {}, detailLoading } = useGetProductionGroupDetail(
    list
  );

  // 查询列表数据
  async function query(params: ListQueryRequestBody) {
    setLoading(true);
    params.query.appId = APP_ID;
    const res: ResponseData<any> = await getRecommendationList(params);

    if (res.code === 200) {
      const { list: resList = [], pagination = {} } = res?.data || {};
      const { total } = pagination;

      if (resList.length > 0) {
        const promiseList = resList.map(
          (item: any) =>
            new Promise((resolve: any, reject: any) => {
              queryCommentDetail(item.commentId, item.subjectId)
                .then((detailRes: ResponseData<any>) => {
                  if (detailRes.code === 200) {
                    resolve(detailRes.data);
                  } else {
                    reject();
                  }
                })
                .catch(() => reject());
            })
        );

        const promiseRes = await Promise.all(promiseList);

        promiseRes.forEach((reportDetailRes: any, index: number) => {
          const { comment = '', checkStatus } = reportDetailRes || {};

          resList[index]['comment'] = comment;
          resList[index]['checkStatus'] = checkStatus;
        });
        setLoading(false);
        setList(resList);
        setTotalCount(total);
        const dramaIdList = resList.map((item: any) => item?.subjectId);

        const dramasInfoRes = await batchGetDramaContentById(dramaIdList);

        // if (dramasInfoRes?.code === 200) {
        //   const dramaInfos = dramasInfoRes?.data || {};
        //   const commentList = resList.map((commentInfo: any) => {
        //     const { subjectId } = commentInfo;

        //     return {
        //       ...commentInfo,
        //       title: dramaInfos?.[subjectId]?.title || '',
        //     };
        //   });

        //   setList(commentList);
        // }

        setList(resList);
      } else {
        setLoading(false);
        setList(resList);
        setTotalCount(total);
      }
    }
  }
  /**
   *
   * @param dealStatus 处理类型 1: 下线评论 2: 不处理
   * @param id
   */
  const dealReportHandle = (dealStatus: number, commentId: string) => {
    dealCommentReport({
      commentId,
      dealStatus,
    }).then((res: ResponseData<null>) => {
      if (res.code === 200) {
        message.success('操作成功！');
        queryTable.current.query();
      }
    });
  };

  const showAllReportUserHandle = (flag: boolean, index: number) => {
    const copyList = [...(list || [])];

    copyList[index]['showAllReportUsers'] = flag;
    setList(copyList);
  };

  const columns: ColumnsType<any> = [
    {
      title: '被举报评论',
      dataIndex: 'comment',
      width: 230,
      render: (text: string) => {
        return (
          text && (
            <div style={{
              maxHeight: '100px',
              wordWrap: 'break-word',
              wordBreak: 'break-all',
              overflowY: 'auto',
            }}
            >
              {text}
            </div>
          )
        );
      },
    },
    {
      title: '名称',
      dataIndex: 'title',
      width: 150,
      render: (text: string, record: any) => {
        return (
          <div>
            {isListContent(record?.subjectType) ? (
              <Link to={ `/content-manage/filmList-manage/add?dramaId=${ record.subjectId }` }
              >
                {productionGroupDetail[record.subjectId]?.title || ''}(片单)
              </Link>
            ) : (
              <Link to={ `/content-manage/production-manage/${ record.subjectId }` }
              >
                {productionGroupDetail[record.subjectId]?.title || ''}
              </Link>
            )}
          </div>
        );
      },
    },
    {
      title: '评论状态',
      dataIndex: 'checkStatus',
      render: (text: number, record: any) => (
        <span style={{ color: text === 2 ? '#ff4d4f' : '#rgba(0, 0, 0, 0.85)' }}
        >
          {REPORT_CHECK_STATUS[text]}
        </span>
      ),
    },
    {
      title: '被举报人',
      dataIndex: 'commentUserName',
      render: (text: string, record: any) => (
        <Link to={ `/user-manage/user/${ record.commentUserId }` }>{text}</Link>
      ),
    },
    {
      title: '举报状态',
      dataIndex: 'dealStatus',
      render: (text: string) => (
        <span style={{ color: statusFontColor[text] }}>
          {REPORT_DEAL_STATUS[text]}
        </span>
      ),
    },
    {
      title: '举报操作',
      dataIndex: 'operate',
      render: (text: string, record: any, index: number) => (
        <Space direction="vertical" align="center">
          {record?.dealStatus === 0 && (
            <Button type="primary"
              size="small"
              onClick={ () => dealReportHandle(2, record.commentId) }
            >
              不处理
            </Button>
          )}
          {record?.dealStatus !== 1 && (
            <Button danger={ true }
              size="small"
              onClick={ () => dealReportHandle(1, record.commentId) }
            >
              下线
            </Button>
          )}
        </Space>
      ),
    },
    {
      title: '操作人',
      dataIndex: 'opUserName',
      width: 170,
      render: (text: string, record: any) =>
        text && (
          <Space direction="vertical" align="center">
            <span>{text}</span>
            <span>{record.dealTime}</span>
          </Space>
        ),
    },
    {
      title: '举报类型',
      dataIndex: 'reportType',
      render: (text: string) => {
        if (['0', '1', '2'].includes(text)) {
          return (
            <div style={{
              backgroundColor: statusFontColor['1'],
              color: '#fff',
              textAlign: 'center',
            }}
            >
              {REPORT_TYPE[text]}
            </div>
          );
        }

        return <div style={{ textAlign: 'center' }}>{REPORT_TYPE[text]}</div>;
      },
    },
    {
      title: '举报人',
      dataIndex: 'reportUsers',
      width: 160,
      render: (reportUsers: any, record: any, index: number) => {
        const { showAllReportUsers } = record;
        const reportUserList
          = reportUsers?.length <= 5 || showAllReportUsers
            ? reportUsers
            : reportUsers?.slice(0, 5);

        return (
          <React.Fragment>
            <Space split="、" wrap={ true }>
              {reportUserList?.map((item: ReportUserInfo) => (
                <Link to={ `/user-manage/user/${ item.userId }` } key={ item.userId }>
                  {item.nickName}
                </Link>
              ))}
            </Space>
            {reportUsers?.length > 5 ? (
              <Button size="small"
                type="primary"
                style={{
                  display: 'inline-block',
                  marginTop: '16px',
                  float: 'right',
                }}
                onClick={ () =>
                  showAllReportUserHandle(!showAllReportUsers, index)
                }
              >
                {showAllReportUsers ? '收起' : '展开'}
              </Button>
            ) : null}
          </React.Fragment>
        );
      },
    },
    {
      title: '举报内容',
      dataIndex: 'reportContent',
      width: 230,
      render: (text: string) =>
        text && (
          <div style={{
            maxHeight: '100px',
            wordWrap: 'break-word',
            wordBreak: 'break-all',
            overflowY: 'auto',
          }}
          >
            {text}
          </div>
        ),
    },
  ];

  const formItem: QueryTableProps['formItem'] = [
    {
      name: 'reportType',
      label: '类型',
      renderCom: 'select',
      comProps: {
        options: Object.keys(REPORT_TYPE).map((key: string) => ({
          label: REPORT_TYPE[key],
          value: key,
        })),
      },
    },
    {
      name: 'dealStatus',
      label: '状态',
      renderCom: 'select',
      comProps: {
        options: Object.keys(REPORT_DEAL_STATUS).map((key: string) => ({
          label: REPORT_DEAL_STATUS[key],
          value: key,
        })),
      },
      itemProps: {
        rules: [{ required: true, message: '请选择状态' }],
        initialValue: '0',
      },
    },
  ];

  return (
    <React.Fragment>
      <QueryTable formItem={ formItem }
        columns={ columns }
        rowKey="commentId"
        formRef={ formRef }
        ref={ queryTable }
        query={ query }
        list={ list || [] }
        total={ totalCount }
        loading={ loading }
      />
    </React.Fragment>
  );
};

export default withAuthorized(ReportList, [Role.SYSTEM, Role.COMMENT]);
// export default ReportList;
