// 调整热度排行
export async function heatAdjust(adjustList: AdjustListItem[]) {
  return await request(
    `${requestBasePath}/wingvate/wind-vane-backend/manager/heat/adjust`,
    {
      method: 'PUT',
      body: adjustList,
    }
  );
}

// 调整热度排行
export async function heatRecommendationAdjust(
  adjustList: AdjustRecommentListItem[]
) {
  return await request(
    `${requestBasePath}/wingvate/wind-vane-backend/manager/heat/recommendation/list`,
    {
      method: 'PUT',
      body: adjustList,
    }
  );
}

