// 保存片单推荐榜
export async function upsertHotList(adjustList: []) {
  return await request(
    `${requestBasePath}/wingvate/wind-vane-backend/hot/operate/upsertHotList`,
    {
      method: 'POST',
      body: { dataList: adjustList },
    }
  );
}
